import { useEffect, useState } from "react";
import axios from "axios";
import { useOutletContext, useParams } from "react-router-dom";

// redux
import { useDispatch } from "react-redux";
import { createMessageAsync } from "../../slice/messageSlice";

function ProductDetail() {
  const dispatch = useDispatch();

  const [product, setProduct] = useState({}); // 單一產品資料
  const [cartQuantity, setCartQuantity] = useState(1); // 購物車數量
  const [isLoading, setIsLoading] = useState(false); // 是否載入中
  const { getCart } = useOutletContext(); // 取得 getCart 方法

  // 取得路由參數 id ，id 會帶在路由參數
  const { id } = useParams();

  // 取得單一產品資料，帶入 id
  const getProduct = async (id) => {
    const productRes = await axios.get(
      `/v2/api/${process.env.REACT_APP_API_PATH}/product/${id}`
    );
    console.log(productRes);
    setProduct(productRes.data.product);
  };

  const addToCart = async () => {
    const data = {
      data: {
        product_id: product.id,
        qty: cartQuantity,
      },
    };
    setIsLoading(true);
    try {
      const res = await axios.post(
        `/v2/api/${process.env.REACT_APP_API_PATH}/cart`,
        data
      );
      console.log(res);
      dispatch(createMessageAsync(res.data));
      getCart();
      setIsLoading(false);
    } catch (error) {
      console.log(error);
      setIsLoading(false);
    }
  };

  useEffect(() => {
    getProduct(id);
  }, [id]);

  return (
    <div className=" full-height">
      <div
        style={{
          minHeight: "300px",
          backgroundImage: `url(${product.imageUrl})`,
          backgroundPosition: "center center",
        }}
      ></div>
      <div className="container">
        <div className="row justify-content-between mt-4 mb-7">
          <div className="col-md-7">
            <div className="my-4">
              <img src={product.imageUrl} alt="" className="img-fluid " />
            </div>
            {/* 渲染 product.imagesUrl 圖片的陣列 */}
            <div className="row">
              <div className="col-3 d-flex">
                {product.imagesUrl &&
                  product.imagesUrl.map((image, index) => (
                    <img
                      src={image}
                      alt=""
                      className="img-fluid me-2"
                      key={index}
                    />
                  ))}
              </div>
            </div>
          </div>
          <div className="col-md-4 my-4">
            <h2 className="mb-0">{product.title}</h2>
            <p className="fw-bold">NT$ {product.price}</p>
            <p>{product.content}</p>
            <div className="input-group mb-3 border rounded-pill mt-3">
              <div className="input-group-prepend">
                <button
                  className="btn btn-outline-dark border-0 py-3 rounded-start-pill product-detail-hover"
                  type="button"
                  id="button-addon1"
                  onClick={() =>
                    setCartQuantity((pre) => (pre === 1 ? pre : pre - 1))
                  }
                >
                  <i className="bi bi-dash"></i>
                </button>
              </div>
              <input
                type="number"
                className="form-control border-0 text-center my-auto shadow-none"
                placeholder=""
                aria-label="Example text with button addon"
                aria-describedby="button-addon1"
                readOnly
                value={cartQuantity}
              />
              <div className="input-group-append">
                <button
                  className="btn btn-outline-dark border-0 py-3 rounded-end-circle product-detail-hover"
                  type="button"
                  id="button-addon2"
                  onClick={() => setCartQuantity((pre) => pre + 1)}
                >
                  <i className="bi bi-plus"></i>
                </button>
              </div>
            </div>
            <div className="d-grid">
              <button
                type="button"
                className="btn btn-light rounded-pill hover-gradient shadow bg-body-tertiary rounded mb-4 py-3"
                disabled={isLoading}
                onClick={() => {
                  addToCart();
                }}
              >
                加入購物車
              </button>
            </div>
            <div
              className="text-secondary d-flex justify-content-end align-items-center"
              data-v-dd60c15d=""
            >
              <span className="me-3" data-v-dd60c15d="">
                分享
              </span>
              <a
                href="https://viccjiang.github.io/react_project/#/"
                className="link-dark"
                data-v-dd60c15d=""
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  className="bi bi-facebook me-3"
                  viewBox="0 0 16 16"
                  data-v-dd60c15d=""
                >
                  <path
                    d="M16 8.049c0-4.446-3.582-8.05-8-8.05C3.58 0-.002 3.603-.002 8.05c0 4.017 2.926 7.347 6.75 7.951v-5.625h-2.03V8.05H6.75V6.275c0-2.017 1.195-3.131 3.022-3.131.876 0 1.791.157 1.791.157v1.98h-1.009c-.993 0-1.303.621-1.303 1.258v1.51h2.218l-.354 2.326H9.25V16c3.824-.604 6.75-3.934 6.75-7.951z"
                    data-v-dd60c15d=""
                  ></path>
                </svg>
              </a>
              <a
                href="https://viccjiang.github.io/react_project/#/"
                className="link-dark"
                data-v-dd60c15d=""
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  className="bi bi-instagram me-3"
                  viewBox="0 0 16 16"
                  data-v-dd60c15d=""
                >
                  <path
                    d="M8 0C5.829 0 5.556.01 4.703.048 3.85.088 3.269.222 2.76.42a3.917 3.917 0 0 0-1.417.923A3.927 3.927 0 0 0 .42 2.76C.222 3.268.087 3.85.048 4.7.01 5.555 0 5.827 0 8.001c0 2.172.01 2.444.048 3.297.04.852.174 1.433.372 1.942.205.526.478.972.923 1.417.444.445.89.719 1.416.923.51.198 1.09.333 1.942.372C5.555 15.99 5.827 16 8 16s2.444-.01 3.298-.048c.851-.04 1.434-.174 1.943-.372a3.916 3.916 0 0 0 1.416-.923c.445-.445.718-.891.923-1.417.197-.509.332-1.09.372-1.942C15.99 10.445 16 10.173 16 8s-.01-2.445-.048-3.299c-.04-.851-.175-1.433-.372-1.941a3.926 3.926 0 0 0-.923-1.417A3.911 3.911 0 0 0 13.24.42c-.51-.198-1.092-.333-1.943-.372C10.443.01 10.172 0 7.998 0h.003zm-.717 1.442h.718c2.136 0 2.389.007 3.232.046.78.035 1.204.166 1.486.275.373.145.64.319.92.599.28.28.453.546.598.92.11.281.24.705.275 1.485.039.843.047 1.096.047 3.231s-.008 2.389-.047 3.232c-.035.78-.166 1.203-.275 1.485a2.47 2.47 0 0 1-.599.919c-.28.28-.546.453-.92.598-.28.11-.704.24-1.485.276-.843.038-1.096.047-3.232.047s-2.39-.009-3.233-.047c-.78-.036-1.203-.166-1.485-.276a2.478 2.478 0 0 1-.92-.598 2.48 2.48 0 0 1-.6-.92c-.109-.281-.24-.705-.275-1.485-.038-.843-.046-1.096-.046-3.233 0-2.136.008-2.388.046-3.231.036-.78.166-1.204.276-1.486.145-.373.319-.64.599-.92.28-.28.546-.453.92-.598.282-.11.705-.24 1.485-.276.738-.034 1.024-.044 2.515-.045v.002zm4.988 1.328a.96.96 0 1 0 0 1.92.96.96 0 0 0 0-1.92zm-4.27 1.122a4.109 4.109 0 1 0 0 8.217 4.109 4.109 0 0 0 0-8.217zm0 1.441a2.667 2.667 0 1 1 0 5.334 2.667 2.667 0 0 1 0-5.334z"
                    data-v-dd60c15d=""
                  ></path>
                </svg>
              </a>
              <a
                href="https://viccjiang.github.io/react_project/#/"
                className="link-dark"
                data-v-dd60c15d=""
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  className="bi bi-twitter"
                  viewBox="0 0 16 16"
                  data-v-dd60c15d=""
                >
                  <path
                    d="M5.026 15c6.038 0 9.341-5.003 9.341-9.334 0-.14 0-.282-.006-.422A6.685 6.685 0 0 0 16 3.542a6.658 6.658 0 0 1-1.889.518 3.301 3.301 0 0 0 1.447-1.817 6.533 6.533 0 0 1-2.087.793A3.286 3.286 0 0 0 7.875 6.03a9.325 9.325 0 0 1-6.767-3.429 3.289 3.289 0 0 0 1.018 4.382A3.323 3.323 0 0 1 .64 6.575v.045a3.288 3.288 0 0 0 2.632 3.218 3.203 3.203 0 0 1-.865.115 3.23 3.23 0 0 1-.614-.057 3.283 3.283 0 0 0 3.067 2.277A6.588 6.588 0 0 1 .78 13.58a6.32 6.32 0 0 1-.78-.045A9.344 9.344 0 0 0 5.026 15z"
                    data-v-dd60c15d=""
                  ></path>
                </svg>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProductDetail;
